.hover-shadow {
    cursor: pointer;
}

.hover-shadow:hover{
    box-shadow:0 .5rem 1rem rgba(0,0,0,.15) !important;
}

.no-text-decor{
    text-decoration: none;
    color: inherit;
    cursor: pointer !important;
}

.no-text-decor:hover{
    text-decoration: none;
    color: inherit;
}

.no-input-shadow:focus{
    box-shadow: none !important;
}

.nav-link.active{
    background: rgba(225, 225, 225, 0.4) !important;
}

.blue-shadow {
    box-shadow:0 .25rem 0.5rem rgba(16, 132, 255, 0.61) !important
}

.red-shadow {
    box-shadow:0 .25rem 0.25rem rgba(255, 81, 81, 0.61) !important
}

.notify-navbar {
    position: relative;
    border-top-left-radius:.25rem!important;
    border-top-right-radius:.25rem!important
}
@media only screen and (max-width: 768px) {
    .notify-navbar {
        position: fixed;
        z-index: 999;
        width: 100%;
        left: 0;
        bottom: 0;
        border-radius: 0;
    }
}